.popup__container {
    width: 430px;
    background: #FFFFFF;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, .15);
    border-radius: 10px;
    position: relative;
}

@media  screen and (max-width: 1279px) {
    .popup__container {
        width: 282px;
        min-height: 322px;
    }  
}